import AbstractView from './abstract-view.js';
import HeaderView from './header-view.js';


const header = new HeaderView();


export default class RulesView extends AbstractView {
  constructor() {
    super();
  }

  get template() {
    return `${header.template}
  <section class="rules">
  <h2 class="rules__title"></h2>
  <ul class="rules__description">
    <li> 10     
      <img class="rules__icon" src="img/icon-photo.png" width="32" height="31" alt="">  
      <img class="rules__icon" src="img/icon-paint.png" width="32" height="31" alt=""></li>
    <li>      .</li>
    <li>    30 .</li>
    <li>    3 .</li>
  </ul>
  <p class="rules__ready">?</p>
  <form class="rules__form">
    <input class="rules__input" type="text" placeholder=" ">
    <button class="rules__button  continue" type="submit" disabled>Go!</button>
  </form>
  </section>`;
  }

  bind() {
    //   bind  HeaderView,      back   onClick()  
    header.bind(this);

    const rulesInput = this.element.querySelector(`.rules__input`);
    const rulesButton = this.element.querySelector(`.rules__button`);
    rulesInput.addEventListener(`input`, () => {
      if (rulesInput.value !== ``) {
        rulesButton.disabled = false;
      } else {
        rulesButton.disabled = true;
      }
    });

    const rulesForm = this.element.querySelector(`.rules__form`);
    rulesForm.addEventListener(`submit`, (evt) => {
      evt.preventDefault();
      this.onSubmit();
      // changeScreen(game);
      // gameScreen(levels[`double`], INITIAL_GAME);
    });
  }

  onClick() { }

  onSubmit() { }

}
