import AbstractView from './abstract-view.js';


export default class Intro extends AbstractView {
  constructor() {
    super();
  }

  get template() {
    return `<section class="intro">
    <button class="intro__asterisk asterisk" type="button"><span class="visually-hidden"></span>*</button>
    <p class="intro__motto"><sup>*</sup>   .     - Tjalf Sparnaay.</p>
    </section>`;
  }

  bind() {
    const introAsterisk = this.element.querySelector(`.intro__asterisk`);
    introAsterisk.addEventListener(`click`, (evt) => {
      evt.preventDefault();
      this.onClick();
    });
  }

  onClick() { }

}
